import React from "react";
import { useFormik } from "formik";
import { useRouter } from "next/router";
import { TagInput } from "./inputs";
import { SingleInput } from "./inputs";
import { searchSchema } from "@/lib/models/candidate";
import { useDispatch } from "react-redux";
import { setSearchQuery } from "@/Features/candidateSearchSlice";

// Define the form values interface
interface SearchFormValues {
  Technologies: string[];
  Companies: string[];
  techRole: string;
  CurrentLocation: string;
  domains: string[];
  preferredLocations: string[];
  qualification: string;
  experience: string;
  salary: string;
  noticePeriod: string;
}

const SearchForm: React.FC = () => {
  const dispatch = useDispatch();
  const router = useRouter();
  const formik = useFormik<SearchFormValues>({
    initialValues: {
      Technologies: ["Java"],
      Companies: ["Google"],
      techRole: "Software Developer",
      CurrentLocation: "",
      domains: ["Banking"],
      preferredLocations: ["Mumbai"],
      qualification: "",
      experience: "",
      salary: "",
      noticePeriod: "",
    },
    validationSchema: searchSchema,
    onSubmit: (values) => {
      const queries = {
        Technologies:
          values.Technologies.length > 0 ? values.Technologies : null,
        Companies: values.Companies.length > 0 ? values.Companies : null,
        techRole: values.techRole || null,
        CurrentLocation: values.CurrentLocation || null,
        domains: values.domains.length > 0 ? values.domains : null,
        preferredLocations:
          values.preferredLocations.length > 0
            ? values.preferredLocations
            : null,
        qualification: values.qualification || null,
        experience: values.experience || null,
        salary: values.salary || null,
        noticePeriod: values.noticePeriod || null,
      };

      // Dispatch to Redux
      dispatch(setSearchQuery(queries));
      router.push("/search/results")
      console.log(values);
    },
  });

  const handleAddTag = (field: keyof SearchFormValues, tag: string) => {
    formik.setFieldValue(field, [...formik.values[field], tag]);
  };

  const handleRemoveTag = (field: keyof SearchFormValues, tag: string) => {
    formik.setFieldValue(
      field,
      Array.isArray(formik.values[field])
        ? formik.values[field].filter((t: string) => t !== tag)
        : formik.values[field]
    );
  };

  return (
    <form onSubmit={formik.handleSubmit}>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 md:text-base text-xs">
        {/* Technologies */}
        <TagInput
          title="Technologies"
          name="Technologies"
          placeholder="Enter Single or Multiple Technologies"
          tags={formik.values.Technologies}
          onChange={(name, updatedTags) =>
            formik.setFieldValue(name, updatedTags)
          }
          showMandatoryButton
          error={
            formik.touched.Technologies ? formik.errors.Technologies : null
          }
        />

        {/* Companies */}
        <TagInput
          title="Companies"
          name="Companies"
          placeholder="Enter Single or Multiple Previous Companies"
          tags={formik.values.Companies}
          onChange={(name, updatedTags) =>
            formik.setFieldValue(name, updatedTags)
          }
          error={formik.touched.Companies ? formik.errors.Companies : null}
        />

        {/* Tech Role */}
        <SingleInput
          title="Tech Role"
          name="techRole"
          placeholder="Enter Tech Role"
          value={formik.values.techRole}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={formik.touched.techRole ? formik.errors.techRole : null}
        />

        {/* Current Location */}
        <SingleInput
          name="CurrentLocation"
          title="Current Location"
          placeholder="Enter Current Location"
          value={formik.values.CurrentLocation}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={
            formik.touched.CurrentLocation
              ? formik.errors.CurrentLocation
              : null
          }
        />

        {/* Domain */}
        <TagInput
          name="domains"
          title="Domain"
          placeholder="Enter Single or Multiple Domains"
          tags={formik.values.domains}
          onChange={(name, updatedTags) =>
            formik.setFieldValue(name, updatedTags)
          }
          error={formik.touched.domains ? formik.errors.domains : null}
        />

        {/* Preferred Location */}
        <TagInput
          name="preferredLocations"
          title="Preferred Location"
          placeholder="Enter Single or Multiple Locations"
          tags={formik.values.preferredLocations}
          onChange={(name, updatedTags) =>
            formik.setFieldValue(name, updatedTags)
          }
          error={
            formik.touched.preferredLocations
              ? formik.errors.preferredLocations
              : null
          }
        />

        {/* Qualification */}
        <SingleInput
          name="qualification"
          title="Qualification"
          placeholder="Enter Qualification Details"
          value={formik.values.qualification}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={
            formik.touched.qualification ? formik.errors.qualification : null
          }
        />

        {/* Experience */}
        <SingleInput
          name="experience"
          title="Experience"
          placeholder="Enter Minimum Experience"
          value={formik.values.experience}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={formik.touched.experience ? formik.errors.experience : null}
        />

        {/* Salary */}
        <SingleInput
          name="salary"
          title="Salary"
          placeholder="Enter Minimum Salary"
          value={formik.values.salary}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={formik.touched.salary ? formik.errors.salary : null}
        />

        {/* Notice Period */}
        <SingleInput
          name="noticePeriod"
          title="Notice Period"
          placeholder="Enter Minimum Days"
          value={formik.values.noticePeriod}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          error={
            formik.touched.noticePeriod ? formik.errors.noticePeriod : null
          }
        />
      </div>

      {/* Submit Button */}
      <div className="flex justify-end mt-6">
        <button
          type="submit"
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
          disabled={!formik.isValid || formik.isSubmitting}
        >
          {formik.isSubmitting ? "Searching..." : "Search Candidates"}
        </button>
      </div>
    </form>
  );
};

export default SearchForm;
