import { useState } from "react";
import { X } from "lucide-react";
import { toast } from "react-toastify";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "../Elements/cards/Card";

interface TagProps {
  text: string;
  onRemove: () => void;
}

export const Tag: React.FC<TagProps> = ({ text, onRemove }) => (
  <div className="bg-zinc-400 text-white flex items-center gap-2 px-2 py-1 rounded-full">
    <span>{text}</span>
    <button
      onClick={onRemove}
      className="bg-black text-white px-1 py-1 rounded-full"
    >
      <X size={12} />
    </button>
  </div>
);

interface TagInputProps {
  title: string;
  placeholder: string;
  tags: string[];
  onAddTag: (tag: string) => void;
  onRemoveTag: (tag: string) => void;
  showMandatoryButton?: boolean;
  error?: string | string[] | null;
}

export const TagInput: React.FC<TagInputProps> = ({
  title,
  placeholder,
  tags,
  onAddTag,
  onRemoveTag,
  showMandatoryButton = false,
  error,
}) => {
  const [input, setInput] = useState("");

  const handleAddTag = () => {
    if (!input) {
      toast.error("Please Enter a Value", {
        position: "top-center",
      });
      return;
    }
    if (input.trim()) {
      onAddTag(input.trim());
      setInput("");
    }
  };

  return (
    <Card className="bg-[var(--field-background)] p-4 h-auto md:h-full space-y-2 md:space-y-4 rounded-lg">
      <CardHeader>
        <CardTitle>{title}</CardTitle>
      </CardHeader>
      <CardContent className="shadow-none space-y-4">
        <input
          type="text"
          placeholder={placeholder}
          value={input}
          onChange={(e) => setInput(e.target.value)}
          className="rounded-sm w-full p-2"
        />
        {error && <div className="text-red-500 text-sm border-red-500">{error}</div>}
        <div className="space-x-6">
          <button
            onClick={handleAddTag}
            className="bg-gray-400 text-white px-4 py-1 rounded"
          >
            Add
          </button>
          {showMandatoryButton && (
            <button
              onClick={() => {
                if (!input) {
                  toast.error("Please Enter a Value", {
                    position: "top-center",
                  });
                  return;
                }
                onAddTag(`${input.trim()}`);
                setInput("");
              }}
              className="bg-blue-500 text-white px-4 py-1 rounded"
            >
              Add as Mandatory
            </button>
          )}
        </div>
        {tags.length > 0 && (
          <div>
            <p className="text-sm mb-2 font-medium">Added {title}:</p>
            <div className="flex flex-wrap gap-2">
              {tags.map((tag, index) => (
                <Tag key={index} text={tag} onRemove={() => onRemoveTag(tag)} />
              ))}
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  );
};

interface SingleInputProps {
  title: string;
  placeholder: string;
  value: string;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onBlur?: (e: React.FocusEvent<HTMLInputElement>) => void;
  error?: string | string[] | null;
}

export const SingleInput: React.FC<SingleInputProps> = ({
  title,
  placeholder,
  value,
  onChange,
  onBlur,
  error,
}) => (
  <Card className="bg-[var(--field-background)] p-4 h-auto md:h-full space-y-2 md:space-y-4 rounded-lg">
    <CardHeader>
      <CardTitle>{title}</CardTitle>
    </CardHeader>
    <CardContent className="space-y-4 shadow-none">
      <input
        type="text"
        className="h-10 w-full rounded-sm p-2"
        placeholder={placeholder}
        value={value}
        onChange={onChange}
        onBlur={onBlur}
      />
      {error && <div className="text-red-500 text-sm border-red-500">{error}</div>}
    </CardContent>
  </Card>
);